body {
    background-color: rgb(165, 26, 165);
    color: white;
    margin: 0;
}
h1 {
    text-transform: uppercase;
    margin-top: 0;
    text-align: center;
}
h2 {
    text-align: center;
}
.flexbox-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.phonebook-container {
    background-color: purple;
    width: 40%;
    min-width: 300px;
    max-width: 700px;
    padding: 20px;
    border: 1px white solid;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.add-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.add-submit-container {
    margin-top: 20px;
}
.contacts {
    font-size: 20px;
}
.delete-btn {
    font-size: 10px;
}

@media only screen and (max-width: 400px) {
    body {
        background-color: purple;
    }
    .phonebook-container {
        border: none;
    }
    .contacts {
        font-size: 16px;
    }
}